<template>
  <div class="user-actions">
    <div class="user-avatar" @click="open = true">
      <img src="@/assets/images/user-avatar-wrapper.svg" alt="User" />
      <span class="user-name"> К </span>
    </div>
    <div class="user-dropdown" 
    :class="{open}"
    >
      <div 
      class="user-dropdown-content" 
      :class="{open: showContent}"
      v-click-outside="closContet"
      >
        <div class="user-actions__close" @click="closContet">
          <img src="@/assets/icons/clear.svg" alt="Close">
        </div>
        <div class="navbar-item navbar-item_name">
          Константин Константинопольский
        </div>
        <div class="navbar-item" @click="closContet">
          <base-link :size="16" :to="{ name: 'settings' }">Настройки</base-link>
        </div>
        <div class="navbar-item" @click="closContet">
          <base-link :size="16" :to="{ name: 'index' }">Выход</base-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import BaseLink from "@/components/common/Base.Link.vue";

export default {
  components: {
    BaseLink,
  },
  data() {
    return {
      open: false,
      showContent: false
    };
  },
  watch: {
    open() {
      if (this.open) {
        setTimeout(() => {
          this.showContent = true
        }, 50);
      }
    }
  },
  methods: {
    closContet() {
      if (this.showContent) {
        this.showContent = false
        setTimeout(() => {
          this.open = false
        }, 300);
      }
    }
  }
};
</script>

